import product from 'assets/images/product.jpg';
import {
  ProductCharactDesc,
  FilterDropdown,
  Counter,
  Button,
} from 'components';
import {
  AmountBtnWrapper,
  BtnsItem,
  BtnsList,
  CharactItem,
  CharactList,
  CharactText,
  CookingMethod,
  CookingMethodImg,
  CookingMethodsItem,
  CookingMethodsList,
  CookingMethodsTitle,
  DescBtn,
  DescBtnsWrapper,
  DescText,
  FiltersWrapper,
  Img,
  ImgWrapper,
  Name,
  Phone,
  Price,
  StockInIcon,
  StockOutIcon,
  StockText,
  StockWrapper,
  WeightBtn,
  Wholesale,
  Wrapper,
  DescWrapper,
} from './Product.styled';
import { useState, useEffect } from 'react';
import { typesOptions } from 'helpers';
import { coffeeFilter, turka, geyser, machine } from 'assets/images';
import { useParams } from 'react-router-dom';
import * as API from 'services/api';

// const weights = [250, 500, 1000];
const weights = [
  { id: 1, value: 250 },
  { id: 2, value: 500 },
  { id: 3, value: 1000 },
];

const descs = [
  { id: 1, value: '  ' },
  { id: 2, value: ' ' },
];

export function Product() {
  const [selectedOption, setSelectedOption] = useState(null);
  const [selectedWeight, setSelectedWeight] = useState(weights[0].value);
  const [selectedDesc, setSelectedDesc] = useState(descs[0].value);
  const { productId } = useParams();

  const inStock = true;

  const onBtnClick = i => {
    setSelectedWeight(weights[i].value);
  };

  const onDescBtnClick = i => {
    setSelectedDesc(descs[i].value);
  };

  useEffect(() => {
    (async function getProduct() {
      try {
        const product = await API.getProductById(productId);
        console.log(product);
        // setMovie(movie);
      } catch (error) {
        // toast.error('Film is not found');
        // navigate('/', { replace: true });
        // console.log(error);
      }
    })();
  }, [productId]);

  return (
    <>
      <Wrapper>
        <ImgWrapper>
          <Img src={product} alt="the photo of product" />
        </ImgWrapper>
        <DescWrapper>
          <Name> Brasil</Name>
          <StockWrapper>
            {inStock ? (
              <>
                <StockInIcon /> <StockText>  </StockText>
              </>
            ) : (
              <>
                <StockOutIcon /> <StockText>  </StockText>
              </>
            )}
          </StockWrapper>
          <ProductCharactDesc />
          <FiltersWrapper>
            <FilterDropdown
              desc={true}
              filterOptions={typesOptions}
              selectedOption={selectedOption}
              onSetOption={option => setSelectedOption(option)}
            />
            <BtnsList>
              {weights.map(({ id, value }, i) => {
                return (
                  <BtnsItem key={id}>
                    <WeightBtn
                      selected={selectedWeight === value ? true : false}
                      type="button"
                      aria-label={`${value}  `}
                      onClick={() => onBtnClick(i)}
                    >
                      {value}
                    </WeightBtn>
                  </BtnsItem>
                );
              })}
            </BtnsList>
          </FiltersWrapper>
          <Wholesale>
               <b></b>    
              <Phone href="tel:+380671429022"></Phone>
          </Wholesale>
          <Price>: 655 </Price>
          <AmountBtnWrapper>
            <Counter />
            <Button
              onClick={() => {
                console.log('add to cart');
              }}
              type="button"
              aria-label="  "
            >
               
            </Button>
          </AmountBtnWrapper>
        </DescWrapper>
      </Wrapper>
      <DescBtnsWrapper>
        {descs.map(({ id, value }, i) => {
          return (
            <DescBtn
              key={id}
              selected={selectedDesc === value ? true : false}
              type="button"
              aria-label={` ${value}`}
              onClick={() => onDescBtnClick(i)}
            >
              {value}
            </DescBtn>
          );
        })}
      </DescBtnsWrapper>
      {selectedDesc === descs[0].value && (
        <>
          <DescText>
                   
                  .   
                    
               .
          </DescText>
          <CharactList>
            <CharactItem>
              <CharactText></CharactText>
              <CharactText>100% </CharactText>
            </CharactItem>
            <CharactItem>
              <CharactText> </CharactText>
              <CharactText></CharactText>
            </CharactItem>
            <CharactItem>
              <CharactText> </CharactText>
              <CharactText></CharactText>
            </CharactItem>
          </CharactList>
        </>
      )}
      {selectedDesc === descs[1].value && (
        <>
          <CookingMethodsTitle>
              :
          </CookingMethodsTitle>
          <CookingMethodsList>
            <CookingMethodsItem>
              <CookingMethodImg src={turka} />
              <CookingMethod></CookingMethod>
            </CookingMethodsItem>
            <CookingMethodsItem>
              <CookingMethodImg src={machine} />
              <CookingMethod> ()</CookingMethod>
            </CookingMethodsItem>
            <CookingMethodsItem>
              <CookingMethodImg src={geyser} />
              <CookingMethod></CookingMethod>
            </CookingMethodsItem>

            <CookingMethodsItem>
              <CookingMethodImg src={coffeeFilter} />
              <CookingMethod> </CookingMethod>
            </CookingMethodsItem>
          </CookingMethodsList>
        </>
      )}
    </>
  );
}
